.navbar {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid #fff;
}
.navbar-brand{
    font-size: 3em;
    color: #fff !important;
    padding: 0px;
}
.navbar.active{
    background: #000;
    padding: 10px;
}

.navbar-brand span {
    color: var(--main) !important;
    font-weight: 600;
    font-size: 1.5em;
}
.navbar-nav{
    padding: 0px 60px;
    margin-top: 40px;
}

.nav-link {
    color: #fff !important;
    margin-left: 5px;
}
.nav-link:hover{
    color: var(--main) !important;
}